
@import url(../node_modules/highlight.js/styles/github.css);
#app{display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row;font-size:.32rem;font-weight:600;color:#7f8c8d
}
#app h2{color:#2c3e50
}
#app ul{list-style:none
}
#app ul li{line-height:2
}
#app ul li span:hover{cursor:pointer;border-bottom:2px solid #42b983
}
#app ul li span.act{color:#42b983
}
#app .nav{height:100%;overflow-y:auto;width:5rem;position:fixed;left:1rem
}
#app .body{position:absolute;left:7rem;width:20rem
}
#app .body>h2,#app .body>h3,#app .body>h4,#app .body>h5{font-weight:400;color:#1f2f3d
}
#app .body>pre{line-height:1.8;font-family:Menlo,Monaco,Consolas,Courier,monospace;font-size:12px;padding:18px 24px;background-color:#f9fafc;border:1px solid #eaeefb;margin-bottom:25px;border-radius:4px;-webkit-font-smoothing:auto
}
#app .body>table{border-collapse:collapse;width:100%;background-color:#fff;color:#5e6d82;font-size:14px;margin-bottom:45px;line-height:1.5em
}
#app .body>table td:first-child,#app .body>table th:first-child{padding-left:10px
}
#app .body>table td,#app .body>table th{border-bottom:1px solid #eaeefb;padding:10px;max-width:250px
}
#app .body>table th{text-align:left;border-top:1px solid #eaeefb;background-color:#eff2f7;white-space:nowrap
}
#app .iframe{background:url("./assets/funny.png") no-repeat;background-size:100%;position:fixed;width:308px;right:2rem;top:50%;margin-top:-388px;height:610px;padding:83px 20px
}
#app .iframe iframe::-webkit-scrollbar{width:1px
}
@media screen and (max-width: 414px){
.iframe{width:100%
}
}



